"use client";

import { motion } from "framer-motion";

export default function About() {
  return (
    <section id="about" className="section-container py-20">
      <motion.div
        initial={{ opacity: 0, y: 30 }}
        whileInView={{ opacity: 1, y: 0 }}
        viewport={{ once: true, amount: 0.4 }}
        transition={{ duration: 0.6 }}
        className="glass-panel rounded-3xl p-6 sm:p-10"
      >
        <div className="flex flex-col gap-6 sm:flex-row sm:items-center sm:justify-between">
          <div className="space-y-3 max-w-2xl">
            <p className="section-title">About</p>
            <h2 className="font-display text-2xl sm:text-3xl text-white">
              Building intelligent, industry-ready systems.
            </h2>
          </div>
          <div className="flex flex-col items-start gap-2 text-xs text-muted sm:text-right sm:items-end">
            <span className="uppercase tracking-[0.22em]">
              Final-year CSE · University of Kashmir
            </span>
            <span>CGPA 7.25</span>
          </div>
        </div>
        <p className="mt-6 text-sm sm:text-base text-muted max-w-3xl">
          Final-year CSE student with strong foundations in{" "}
          <span className="text-accent">
            Python, data analysis, and database systems
          </span>
          . Passionate about designing and deploying{" "}
          <span className="text-accent">
            AI-driven, data-centric solutions
          </span>{" "}
          that solve real-world problems in Machine Learning and Data Science.
          I enjoy taking projects end-to-end — from data cleaning and feature
          engineering to model training, evaluation, and full stack deployment.
        </p>
      </motion.div>
    </section>
  );
}

