"use client";

import { useEffect, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Image from "next/image";
import { FiX, FiChevronLeft, FiChevronRight } from "react-icons/fi";

const galleryImages = [
  "gallery-1.jpg",
  "gallery-2.jpg",
  "gallery-3.jpg",
  "gallery-4.jpg",
  "gallery-5.jpg",
  "gallery-6.jpg",
  "gallery-7.jpg",
  "gallery-8.jpg"
];

export default function Gallery() {
  const [activeIndex, setActiveIndex] = useState<number | null>(null);

  const close = () => setActiveIndex(null);
  const showPrev = () =>
    setActiveIndex((prev) =>
      prev === null ? null : (prev - 1 + galleryImages.length) % galleryImages.length
    );
  const showNext = () =>
    setActiveIndex((prev) =>
      prev === null ? null : (prev + 1) % galleryImages.length
    );

  useEffect(() => {
    if (activeIndex === null) return;
    const onKey = (event: KeyboardEvent) => {
      if (event.key === "Escape") close();
      if (event.key === "ArrowLeft") showPrev();
      if (event.key === "ArrowRight") showNext();
    };
    window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, [activeIndex]);

  return (
    <section id="gallery" className="section-container py-20">
      <div className="flex flex-col gap-6 sm:flex-row sm:items-end sm:justify-between mb-8">
        <div className="space-y-3">
          <p className="section-title">Gallery</p>
          <h2 className="font-display text-2xl sm:text-3xl">
            Moments, systems, and stories.
          </h2>
        </div>
        <p className="max-w-md text-sm text-muted">
          A visual snapshot of projects, experiments, and experiences that
          influence how I think about systems, performance, and design.
        </p>
      </div>
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        {galleryImages.map((file, idx) => (
          <motion.button
            key={file}
            type="button"
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, amount: 0.3 }}
            transition={{ duration: 0.4, delay: idx * 0.02 }}
            whileHover={{ scale: 1.03, y: -4 }}
            className="relative overflow-hidden rounded-2xl border border-white/10 bg-black/40 group focus:outline-none focus-visible:ring-2 focus-visible:ring-accent"
            onClick={() => setActiveIndex(idx)}
          >
            <div className="absolute inset-0 bg-gradient-to-t from-black/40 to-transparent opacity-70 group-hover:opacity-100 transition-opacity" />
            <Image
              src={`/gallery/${file}`}
              alt={`Gallery image ${idx + 1}`}
              width={480}
              height={480}
              className="h-full w-full object-cover transition-transform duration-500 group-hover:scale-110"
            />
            <div className="absolute inset-0 opacity-0 group-hover:opacity-100 transition-opacity">
              <div className="absolute inset-0 rounded-2xl border border-accent/60 shadow-glow" />
            </div>
          </motion.button>
        ))}
      </div>

      <AnimatePresence>
        {activeIndex !== null && (
          <motion.div
            className="fixed inset-0 z-50 flex items-center justify-center bg-black/80 backdrop-blur-md"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
          >
            <button
              type="button"
              aria-label="Close lightbox"
              onClick={close}
              className="absolute top-6 right-6 rounded-full border border-white/20 bg-black/60 p-2 text-white hover:text-accent transition-colors"
            >
              <FiX size={20} />
            </button>
            <button
              type="button"
              aria-label="Previous image"
              onClick={showPrev}
              className="absolute left-6 rounded-full border border-white/20 bg-black/60 p-2 text-white hover:text-accent transition-colors"
            >
              <FiChevronLeft size={22} />
            </button>
            <button
              type="button"
              aria-label="Next image"
              onClick={showNext}
              className="absolute right-6 rounded-full border border-white/20 bg-black/60 p-2 text-white hover:text-accent transition-colors"
            >
              <FiChevronRight size={22} />
            </button>
            <motion.div
              initial={{ opacity: 0, scale: 0.95 }}
              animate={{ opacity: 1, scale: 1 }}
              exit={{ opacity: 0, scale: 0.95 }}
              transition={{ duration: 0.25 }}
              className="relative w-[90vw] max-w-3xl rounded-3xl border border-white/10 bg-black/60 p-3 sm:p-4"
            >
              <Image
                src={`/gallery/${galleryImages[activeIndex]}`}
                alt={`Gallery image ${activeIndex + 1}`}
                width={1200}
                height={800}
                className="h-auto w-full rounded-2xl object-contain"
              />
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </section>
  );
}

