"use client";

import { motion } from "framer-motion";

const projects = [
  {
    title: "SmartHire (In Progress)",
    description:
      "AI-driven recruitment assistant using Python, React, and Scikit-learn to automate candidate screening and shortlist generation.",
    tech: ["Python", "React", "Scikit-learn", "Pandas", "REST APIs"],
    link: "https://github.com/meersuhaib191",
    status: "In Progress"
  },
  {
    title: "Customer Subscription Data Analysis",
    description:
      "End-to-end subscription analytics: data cleaning, EDA, visualization, and a Power BI dashboard for business insights.",
    tech: ["Python", "Pandas", "Matplotlib", "Seaborn", "Power BI"],
    link: "https://github.com/meersuhaib191",
    status: "Completed"
  },
  {
    title: "Hostel Ease",
    description:
      "Web-based Hostel Management System for student allocation, records, and communication using PHP and SQL.",
    tech: ["PHP", "MySQL", "Bootstrap", "PHPMailer"],
    link: "https://github.com/meersuhaib191",
    status: "Completed"
  },
  {
    title: "Library Management System",
    description:
      "Library portal with book inventory, issue/return workflows, and basic reporting built with PHP and MySQL.",
    tech: ["PHP", "MySQL", "JavaScript", "HTML", "CSS", "Bootstrap"],
    link: "https://github.com/meersuhaib191",
    status: "Completed"
  }
];

export default function Projects() {
  return (
    <section id="projects" className="section-container py-20">
      <div className="flex flex-col gap-6 sm:flex-row sm:items-end sm:justify-between mb-8">
        <div className="space-y-3">
          <p className="section-title">Projects</p>
          <h2 className="font-display text-2xl sm:text-3xl">
            From raw data to deployed systems.
          </h2>
        </div>
        <p className="max-w-md text-sm text-muted">
          A snapshot of projects spanning ML models, analytical dashboards, and
          full stack web applications — all optimized for clarity, reliability,
          and user impact.
        </p>
      </div>
      <div className="grid gap-6 md:grid-cols-2">
        {projects.map((project, idx) => (
          <motion.article
            key={project.title}
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, amount: 0.3 }}
            transition={{ duration: 0.5, delay: idx * 0.05 }}
            whileHover={{ y: -6, scale: 1.01 }}
            className="glass-panel rounded-2xl p-5 sm:p-6 flex flex-col justify-between"
          >
            <div>
              <div className="flex items-center justify-between gap-3 mb-3">
                <h3 className="font-display text-lg text-white">
                  {project.title}
                </h3>
                <span className="text-[10px] uppercase tracking-[0.2em] text-muted border border-white/10 rounded-full px-3 py-1">
                  {project.status}
                </span>
              </div>
              <p className="text-sm text-muted mb-4">{project.description}</p>
              <div className="flex flex-wrap gap-2 mb-4">
                {project.tech.map((tech) => (
                  <span
                    key={tech}
                    className="text-[11px] rounded-full border border-white/10 bg-white/5 px-2.5 py-1 text-muted"
                  >
                    {tech}
                  </span>
                ))}
              </div>
            </div>
            <div className="flex items-center justify-between pt-2">
              <a
                href={project.link}
                target="_blank"
                rel="noreferrer"
                className="inline-flex items-center gap-2 text-xs font-semibold uppercase tracking-[0.2em] text-accent hover:text-accentOrange transition-colors"
              >
                <span>View on GitHub</span>
                <span className="h-[1px] w-6 bg-gradient-to-r from-accent to-accentOrange" />
              </a>
              <div className="h-8 w-8 rounded-full border border-white/10 flex items-center justify-center text-[11px] text-muted">
                F1
              </div>
            </div>
          </motion.article>
        ))}
      </div>
    </section>
  );
}

