"use client";

import { motion } from "framer-motion";

const categories = [
  {
    title: "Programming & Tools",
    items: ["Python", "React", "MySQL", "HTML", "CSS", "JavaScript", "PHP"]
  },
  {
    title: "Scientific & Dev Tools",
    items: [
      "Jupyter Notebook",
      "Google Colab",
      "Git",
      "GitHub",
      "Excel",
      "Power BI"
    ]
  },
  {
    title: "Libraries & Frameworks",
    items: [
      "Pandas",
      "NumPy",
      "Matplotlib",
      "Seaborn",
      "Scikit-learn",
      "SciPy"
    ]
  },
  {
    title: "Databases",
    items: ["MySQL", "MongoDB"]
  }
];

export default function Skills() {
  return (
    <section id="skills" className="section-container py-20">
      <div className="flex flex-col gap-6 sm:flex-row sm:items-end sm:justify-between mb-8">
        <div className="space-y-3">
          <p className="section-title">Skills</p>
          <h2 className="font-display text-2xl sm:text-3xl">
            A toolkit for intelligent systems.
          </h2>
        </div>
        <p className="max-w-md text-sm text-muted">
          From data pipelines and exploratory analysis to model development and
          full stack delivery, this stack is tuned for building high-impact,
          production-ready solutions.
        </p>
      </div>
      <div className="grid gap-6 md:grid-cols-2">
        {categories.map((category, idx) => (
          <motion.div
            key={category.title}
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, amount: 0.3 }}
            transition={{ duration: 0.5, delay: idx * 0.05 }}
            className="glass-panel rounded-2xl p-5 sm:p-6 relative overflow-hidden group"
          >
            <div className="absolute inset-0 opacity-0 group-hover:opacity-100 transition-opacity duration-300 pointer-events-none">
              <div className="absolute -inset-24 bg-[conic-gradient(from_180deg_at_50%_50%,rgba(0,245,255,0.18),transparent,rgba(255,122,26,0.18),transparent)] blur-3xl" />
            </div>
            <div className="relative">
              <h3 className="font-display text-sm uppercase tracking-[0.2em] mb-3 text-muted">
                {category.title}
              </h3>
              <div className="flex flex-wrap gap-2">
                {category.items.map((item) => (
                  <motion.span
                    key={item}
                    whileHover={{ scale: 1.02 }}
                    className="text-xs rounded-full border border-white/10 bg-white/5 px-3 py-1 text-muted group-hover:shadow-glow-soft group-hover:border-accent/70 group-hover:text-accent transition-all"
                  >
                    {item}
                  </motion.span>
                ))}
              </div>
            </div>
          </motion.div>
        ))}
      </div>
    </section>
  );
}

